<template>
	<view  class="start">
		<scroll-view class="scroll-v list" lower-threshold="200" enableBackToTop="true" scroll-y="true" @scrolltolower="loadMore">
			<view class="loading-more" v-if="newsList.isLoading || newsList.data.length == 0">
				<text class="loading-more-text">{{newsList.noneText}}</text>
			</view>
			<view class="uni-list-cell "  v-for="(newsitem,index2) in newsList.data" :key="newsitem.idx"  @close="close(index2)" >
				<view class="uni-media-list ">
					<view class="uni-media-list-body ">
						<view class="uni-media-list-text-top" @click="goDetail(newsitem)">{{ getDictV(dictProType,newsitem.type) }}<text class="active-text">({{getDictV(statusV,newsitem.status)}})</text></view>
						<view class="uni-media-list-text-bottom ">
							<text>发起人：{{ newsitem.userName }}</text>
							<text class="mini-text">{{ newsitem.createTime }}</text>
						</view>
					</view>
				</view>
			</view>
			<view class="loading-more" v-if="newsList.isLoading || newsList.data.length > 9">
				<text class="loading-more-text">{{newsList.loadingText}}</text>
			</view>
		</scroll-view>
	</view>
</template>

<script>
	// 缓存每页最多
	const MAX_CACHE_DATA = 100;
	// 缓存页签数量
	const MAX_CACHE_PAGE = 3;
	export default {
		data() {
		    return {
				user:{ },
				newsList: {
				  data: [],
				  isLoading: false,
				  refreshText: "",
				  loadingText: '加载更多...',
				  noneText:'加载中.....',
				  total:0,
				  pageSize:10
				},
				tabBars: {
				    name: '催办',
				    id: 'c001',
					url:'/oa/MyApplied/phoneList',
					pageNum:1
				},
				scrollInto: "",
				navigateFlag: false,
				pulling: false,
				refreshIcon: "data:image/png;base64,",
				dictProType:[],
				statusV:[]
			}
		},
	    onReady() {
			/**
			 * 请求登陆以后的数据
			 */
			let this_=this
			this.urlRequestPost('/phoneIndexData','',null,function(res){
				console.log(res)
				if(res.statusCode='200'){
					this_.user=res.data.user
				}
				
			})
			/**
			 * 字典数据-审批类型
			 */
			this.dictValue('oa_examine_type').then(res => {
				this_.dictProType=res
			})
			this.dictValue('oa_leave_status').then(res => {
				this_.statusV=res
			})
			this.getList()
			
	    },
		methods: {
			/**
			 * @param {index} 第几个活页
			 * 获取活页里面的list数据
			 */
			getList() {
				this.serverList(this.tabBars.url)
			},
			/**
			 * 请求接口数据
			 */
			serverList(url){
				let activeTab = this.newsList;
				const this_=this
				console.log('start')
				this.urlRequestPost(url,{'pageSize':this.newsList.pageSize,'pageNum':this.tabBars.pageNum,'orderBy':'create_time desc'},null,function(res){
					if(res.data.rows.length!=0){
						this_.newsList.noneText='加载中.....'
						let list = [];
						
						for (let i = 0; i < res.data.rows.length; i++) {
							let item =res.data.rows[i]
							item.idx = this_.newGuid();
							list.push(item);
						}
						if(this_.tabBars.pageNum<=1){
							activeTab.data = list;
						}else{
							activeTab.data = activeTab.data.concat(list);
						}
						this_.newsList.total=res.data.total
						console.log(this_.newsList.data)
					}else{
						this_.newsList.noneText='暂无数据!'
					}
					
					
				})
			},
			 loadMore() {
				 setTimeout(() => {
					 console.log('...')
					 console.log(this.newsList.total)
					let uPageNum=Math.ceil(this.newsList.total/this.newsList.pageSize)
					console.log(uPageNum)
					if(this.tabBars.pageNum<uPageNum){
						this.tabBars.pageNum++
						this.getList();
					}else{
						this.newsList.loadingText='没有数据了！'
					}
					
				 }, 500)
			 },
			 /**
			  * @param {Object} e
			  */
			 goDetail(e) {
			 	let this_=this
				let url = ''
				let id=''
			     if (this.navigateFlag) {
			         return;
			     }
			     this.navigateFlag = true;
				 switch(e.type){
					 case '1': case '11': case '12'://请假申请
					      url='../oa/leave/leave'
						  break
					case '0'://出差申请
					      url='../oa/travel/travel'
						  break
					case '200'://外出申请
					      url='../oa/outgoing/outgoing'
						  break
					case '210'://补卡申请
					      url='../oa/supplyCard/supplyCard'
						  break
					case '220'://加班申请
					      url='../oa/overtime/overtime'
						  break
				    case '3'://报销申请
				          url='../oa/reimbursement/reimbursement'
				    	  break
					case '2': case '21'://采购申请
					      url='../oa/purchase/purchase'
						  break
					case '5': case '51': case '52': case '53': case '54'://用印申请
					      url='../oa/sealApply/sealApply'
						  break
					case '4': case '41'://用车申请
					      url='../oa/carApply/carApply'
						  break
				 }
				 uni.navigateTo({
				 	url: url+"?edit=1&id="+e.id,
				 });
			     setTimeout(() => {
			         this.navigateFlag = false;
			     }, 200)
			 },
			 clearTabData(e) {
			     this.newsList.data.length = 0;
			     this.newsList.loadingText = "加载更多...";
			 },
			 refreshData() {},
			 onrefresh(e) {
			     var tab = this.newsList;
			     if (!tab.refreshFlag) {
			         return;
			     }
			     tab.refreshing = true;
			     tab.refreshText = "正在刷新...";
			 		
			     setTimeout(() => {
			         this.refreshData();
			         this.pulling = true;
			         tab.refreshing = false;
			 		tab.refreshFlag = false;
			         tab.refreshText = "已刷新";
			         setTimeout(() => { // TODO fix ios和Android 动画时间相反问题
			             this.pulling = false;
			         }, 500);
			     }, 2000);
			 },
			 onpullingdown(e) {
			     var tab = this.newsList;
			     if (tab.refreshing || this.pulling) {
			         return;
			     }
			     if (Math.abs(e.pullingDistance) > Math.abs(e.viewHeight)) {
			         tab.refreshFlag = true;
			         tab.refreshText = "释放立即刷新";
			     } else {
			         tab.refreshFlag = false;
			         tab.refreshText = "下拉可以刷新";
			     }
			 },
			 newGuid() {
			     let s4 = function() {
			         return (65536 * (1 + Math.random()) | 0).toString(16).substring(1);
			     }
			     return (s4() + s4() + "-" + s4() + "-4" + s4().substr(0, 3) + "-" + s4() + "-" + s4() + s4() + s4()).toUpperCase();
			 },
			 getDictV(type,value){
			 	let dictV=''
			 	for(let i in type.data){
			 		if(type.data[i].dictValue==value){
			 			dictV=type.data[i].dictLabel
			 		}
			 	}
			 	return dictV
			 }
			 
		}
	}
	
</script>

<style>
    /* #ifndef APP-PLUS */
    page {
        width: 100%;
        min-height: 100%;
        display: flex;
		background:#f8f8f8;
    }
    .start{
		width: 100%;
		height: 1800upx;
	}
    /* #endif */
    .scroll-v {
		flex: 1;
		width: 100%;
		height: 1800upx; 
    }
    
    .loading-icon {
    	width: 20px;
    	height: 20px;
    	margin-right: 5px;
    	color: #999999;
    }
    
    .loading-more {
        align-items: center;
        justify-content: center;
        padding-top: 10px;
        padding-bottom: 10px;
        text-align: center;
    }
    
    .loading-more-text {
        font-size: 28upx;
        color: #999;
    }
	
	
	.uni-media-list-body {
		height: auto;
		justify-content: space-around;
	}
	
	.uni-media-list-text-top {
		height: 74upx;
		font-size: 28upx;
		overflow: hidden;
	}
	
	.uni-media-list-text-bottom {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
	}
	.mini-text{
		color: #a7a7a7;
	}
	.active-text{
		color: #0FAEFF;
	}
	.uni-media-list {
		width: 100%;
		padding: 35upx 30upx;
	}
	.uni-list-cell::after{
		width: 100%;
		left: 0;
	}
	
	.uni-list-cell {
		height: 180upx;
	}
</style>
